*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
body{
    font-family: 'Silkscreen', cursive;
    color: #eee;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    height: 100vh;
    background-image: url("https://studioghiblimovies.com/wp-content/uploads/2017/12/1.gif");
    background-position: 20%;
    background-size: cover;
    background-repeat: no-repeat;

}

.cover-img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: #000000d1;
    background-size: cover;
    opacity: 0.5;
    z-index: 0;
}

.container{
    margin: 10px;
    min-width: 350px;
    background-color: rgba(255, 255, 255, 0.116);
    box-shadow: 0 4px 10px rgba(0,0,0,0.6);
    position: relative;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    border-radius: 5px;
}
.header{
    padding: 1rem;
}
.title{
    width: 100%;
    height: 70px;
    /* background-color: rgba(0, 0, 0, 0.392); */
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    font-size: 1.5rem;
    padding: 1rem;
    border-radius: 5px;
}

#title{
    font-size: 21px;
}
#len{
    background-color: #0f0f0f73;
    border: none;
    width: 57px;
    padding-left: 10px;
    color: white;
    border-radius: 5px;
    font-size: 15px;
    font-family: 'Silkscreen', cursive;
}
#calc{
    background-color: #0f0f0f73;
    border: none;
    width: 75px;
    padding-left: 10px;
    color: white;
    border-radius: 5px;
    font-size: 14px;
    font-family: 'Silkscreen', cursive;
}
.title:hover button{
    opacity: 1;
    transform: translate(0, -80%);
    
}
.t-body{
    padding: 0 1rem 1rem;

}
.form-control{
    color: #eee;
    display: flex;
    justify-content: space-between;
    margin: 0.75rem 0;
}

/* Nav section */
.nav-list{
    list-style: none;
    margin-left: 11px;
    margin-right: 11px;
    font-size: 13px;
    display: flex;
    justify-content: space-between;
    z-index: 4;
}
.link{
    text-decoration: none;
    color: rgb(129, 222, 115);
    position: relative;
}
